/*Список*/
.List{
    padding: 10px;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 300px));
    grid-column-gap: 20px;
    grid-row-gap: 20px;
    justify-content: center;
}
.List-item{
    display: flex;
}

.List-item:first-child{
    border-top: 1px dashed #ccc;
}
